export default {
	props: {
		item: Object,
		index: Number
	},
	data() {
		return { loading: false };
	},
	methods: {
		// 跳转
		openUrl() {
				let url_31 = '../../pages/crejianliup/crejianliup?item=' + encodeURIComponent(JSON.stringify(this.item));
				uni.navigateTo({
					url: url_31
				});
				return true;
		},
		//操作
		operation(type) {
			let data = {};
			data.type = type;
			data.userId = this.item.userId;
			data.id = this.item.id;
			if (type === 1 || type === 2 || type === 4 || type === 5) {
				this.operationRequest(data);
			} else if (type === 3) {
				let this_ = this;
				uni.showModal({
					title: '确认要删除',
					content: '',
					success: function(res) {
						if (res.confirm) {
							this_.operationRequest(data);
						} else if (res.cancel) {
						}
					}
				});
			} else {
				uni.showToast({
					title: '操作类型错误',
					icon: 'none'
				});
			}
		},
		operationRequest(data) {
			this.loading = true;
			this.$http.post(this.$urlconfig.operation_resume, data, {}).then(data => {
				this.loading = false;
				if (data.status === 0) {
					uni.showToast({
						title: '操作成功',
						icon: 'none'
					});
					this.$emit('getnew');
				}
			});
		}
	}
};
